body {
    background-color: lightgray;
}


.btn {
    margin: 10px 0px 10px 10px;
    background: -webkit-gradient( linear, left top, left bottom, from(#89dfec), color-stop(60%, #00c0de), to(#89dfec));
    background: -webkit-linear-gradient( #89dfec, #00c0de 60%, #89dfec);
    background: -o-linear-gradient( #89dfec, #00c0de 60%, #89dfec);
    background: linear-gradient( #89dfec, #00c0de 60%, #89dfec);
    color: white;
}

div.paper {
    -webkit-transition: all 235ms ease 0s;
    -o-transition: all 235ms ease 0s;
    transition: all 235ms ease 0s;
    -webkit-box-shadow: 0.5vh 1vh 1vh black, 0 0 2vh black, 0 0 5px #00dbff;
            box-shadow: 0.5vh 1vh 1vh black, 0 0 2vh black, 0 0 5px #00dbff;
    -webkit-transition: all 235ms ease-in-out 0s;
    -o-transition: all 235ms ease-in-out 0s;
    transition: all 235ms ease-in-out 0s;
    -ms-flex-item-align: center;
        align-self: center;
    background: 0 0;
    border: 0.2vh solid #41403e;
    color: #00dbff;
}


